<template>
  <div class="accreditMes">

    <div class="w1660fr">
      <div class="detailBtn">
        <span class="detailNav"></span>
        <router-link to="/portManage">
          <el-button size="mini">API</el-button>
        </router-link>
      </div>

      <el-table :data="historyData" border style="width: 100%" :row-class-name="tableRowClassName">
        <el-table-column prop="versionId" label=""></el-table-column>
        <el-table-column prop="pubDescription" label=""></el-table-column>

        <el-table-column prop="env" label="" filter-placement="bottom-end" :filter-multiple="false" column-key="runTimeEnv">
          <template slot-scope="scope">
            <el-tag close-transition>{{scope.row.env == 1?'':''}}</el-tag>
          </template>
        </el-table-column>

        <el-table-column prop="createTime" label="" :formatter="dateFormat"></el-table-column>
        <el-table-column prop="" label="">
          <template slot-scope="scope">
            <el-button type="text" size="small" class="radiusBtn" @click="lookData(scope.row)"></el-button>
            <el-button type="text" size="small" class="radiusBtn" @click="switchoverData(scope.row)" v-if="scope.row.currentVersion != 1 && vsionBtn"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- API start -->
      <el-dialog title="API" :visible.sync="look" style="height:800px;overflow:auto">
        <div class="panel panel-default">
          <div class="console-panel-header">
            <span class="panel-header"></span>
            <table class="table table-hover table-bordered">
              <tbody v-model="apiInfoVersion" style="margin-bottom:10px;position:relative">
                <p style="position:absolute;top:20px;left:115px">
                  <b>{{apiInfoVersion.versionId}}</b></p>
                <tr>
                  <td>
                    <span>{{apiInfoVersion.groupName}}</span>
                  </td>
                  <td>
                    <span>{{apiInfoVersion.apiName}}</span>
                  </td>
                </tr>

                <tr>
                  <td>
                    <span>{{apiInfoVersion.saveMongoDB==1?'':''}}</span>
                  </td>
                  <td>MQ
                    <span>{{apiInfoVersion.saveMQ==1?'':''}}</span>
                  </td>
                </tr>

                <tr>
                  <td>Mongodb
                    <span>{{apiInfoVersion.mongodbURI}}</span>
                  </td>
                  <td>Mongodb:
                    <span>{{apiInfoVersion.mongodbDBName}}</span>
                  </td>
                </tr>

                <tr>
                  <td>Mongodb
                    <span>{{apiInfoVersion.mongodbCollectionName}}</span>
                  </td>
                  <td>
                    <span>{{apiInfoVersion.apiDescription}}</span>
                  </td>
                </tr>

                <tr>
                  <td>MQ
                    <span>{{apiInfoVersion.mqType ==1 ?'rabbbitmq':'kafka'}}</span>
                  </td>
                  <td>MQ
                    <span>{{apiInfoVersion.mqAddress}}</span>
                  </td>
                </tr>

                <tr>
                  <td>
                    <span>{{apiInfoVersion.mqUserName}}</span>
                  </td>
                  <td>
                    <span>{{apiInfoVersion.mqPasswd}}</span>
                  </td>
                </tr>

                <tr>
                  <td colspan="2">
                    <span>{{apiInfoVersion.mqTopicName}}</span>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>

        <div class="panel panel-default">
          <div class="console-panel-header">
            <span class="panel-header"></span>
            <table v-model="apiInfoVersion" class="table table-hover table-bordered">
              <tbody>
                <tr>
                  <td colspan="2">A
                    <span>{{apiInfoVersion.backEndAddress}}</span>
                  </td>
                </tr>
                <tr>
                    <td colspan="2">B
                      <span>{{apiInfoVersion.backEndAddressB}}</span>
                    </td>
                  </tr>
                <tr>
                  <td>Mock
                    <span>{{apiInfoVersion.isMock ==1?'mock':'mock'}}</span>
                  </td>
                  <td>
                    <span>{{apiInfoVersion.backEndTimeout}}</span> ms</td>
                </tr>
                <tr>
                  <td colspan="2">Mock
                    <span>{{apiInfoVersion.mockData}}</span>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>

        <div class="panel panel-default">
          <div class="console-panel-header">
            <span class="panel-header"></span>
            <table v-model="apiInfoVersion" class="table table-hover table-bordered">
              <tbody>
                <tr>
                  <td>Path
                    <span>{{apiInfoVersion.httpPath}}</span>
                  </td>
                  <td>
                    <span></span>
                  </td>
                </tr>
                <tr>
                  <td colspan="2">HTTP Method
                    <span>{{apiInfoVersion.httpMethod ==1 ? 'GET': 'POST'}}</span>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>

        <div class="panel panel-default" style="position:relative;">
          <div class="console-panel-header">
            <span class="panel-header"></span>
            <table class="table table-hover table-bordered">
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody v-if="requestParamsVersionsList.length <= 0">
                <tr>
                  <td colspan="8" style="text-align:center">
                    <span></span>
                  </td>
                </tr>
              </tbody>
              <tbody v-if="requestParamsVersionsList.length > 0">
                <tr v-for="(item,index) in requestParamsVersionsList">
                  <td>{{item.paramName}}</td>
                  <td>
                    <span v-if="item.paramsType == 1">String</span>
                    <span v-else-if="item.paramsType == 2">Integer</span>
                    <span v-else-if="item.paramsType == 3">Long</span>
                    <span v-else-if="item.paramsType == 4">Float</span>
                    <span v-else-if="item.paramsType == 5">Double</span>
                    <span v-else-if="item.paramsType == 6">Boolean</span>
                  </td>
                  <td>
                    <span>{{item.paramsMust == 1 ?'':''}}</span>
                  </td>
                  <td>{{item.paramsDefaultValue}}</td>
                  <td>{{item.paramsExample}}</td>
                  <td>{{item.paramsDescription}}</td>
                  <td>{{item.paramName}}</td>
                  <td>
                    <a href="javascript:void(0)" style="padding:0 8px;color:#1C72EE" class="radiusBtn" @mouseover="moreStart(index)" @mouseleave="moreEnd(index)"></a>

                    <el-form class="moreMes din" style="width:400px;">
                      <el-form-item label="" :label-width="formLabelWidth">
                        <span>{{requestParamsVersionsList[amendApiIndex].maxLength}}</span>
                      </el-form-item>

                      <el-form-item label="" :label-width="formLabelWidth">
                        <span>{{requestParamsVersionsList[amendApiIndex].minLength}}</span>
                      </el-form-item>

                      <el-form-item label="" :label-width="formLabelWidth">
                        <span>{{requestParamsVersionsList[amendApiIndex].regularExpress}}</span>
                      </el-form-item>
                    </el-form>
                    <!--  end -->
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>

        <div class="panel panel-default">
          <div class="console-panel-header">
            <span class="panel-header"></span>
            <table class="table table-hover table-bordered">
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody v-if="backendParamsList.length <= 0">
                <tr>
                  <td colspan="5" style="text-align:center">
                    <span></span>
                  </td>
                </tr>
              </tbody>
              <tbody v-if="backendParamsList.length > 0">
                <tr v-for="(item,index) in backendParamsList">
                  <td>{{item.paramName}}</td>
                  <td>
                    <span v-if="item.paramsLocation == 1">header</span>
                    <span v-else-if="item.paramsLocation == 2">query</span>
                    <span v-else-if="item.paramsLocation == 3">body</span>
                  </td>
                  <td>
                    <span>{{requestParamsVersionsList[index].paramName}}</span>
                  </td>
                  <td>
                    <span v-if="requestParamsVersionsList[index].paramsLocation == 1">header</span>
                    <span v-else-if="requestParamsVersionsList[index].paramsLocation == 2">query</span>
                    <span v-else-if="requestParamsVersionsList[index].paramsLocation == 3">body</span>
                  </td>
                  <td>
                    <span v-if="requestParamsVersionsList[index].paramsType == 1">String</span>
                    <span v-else-if="requestParamsVersionsList[index].paramsType == 2">Integer</span>
                    <span v-else-if="requestParamsVersionsList[index].paramsType == 3">Long</span>
                    <span v-else-if="requestParamsVersionsList[index].paramsType == 4">Float</span>
                    <span v-else-if="requestParamsVersionsList[index].paramsType == 5">Double</span>
                    <span v-else-if="requestParamsVersionsList[index].paramsType == 6">Boolean</span>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>

        <div class="panel panel-default">
          <div class="console-panel-header">
            <span class="panel-header"></span>
            <el-table :data="constParamsList" style="width: 100%">
              <el-table-column prop="paramName" label=""></el-table-column>
              <el-table-column prop="paramValue" label=""></el-table-column>
              <el-table-column prop="paramsLocation" label="" :formatter="paramLocationFomatter"></el-table-column>
              <el-table-column prop="paramDescription" label=""></el-table-column>
            </el-table>
          </div>
        </div>

        <div class="panel panel-default">
          <div class="console-panel-header">
            <span class="panel-header"></span>
            <table v-model="apiInfoVersion" class="table table-hover table-bordered">
              <tbody>
              <tr>
                <td colspan="2">
                  <span>{{apiInfoVersion.callBackType ==1?'JSON':''}}</span>
                </td>
              </tr>
              <tr>
                <td style="float:left;width:50%;height:200px;">
                  <span class="writePlaces" style="width:310px;display:block;height:160px;overflow:auto;">{{apiInfoVersion.callBackSuccessExample}}</span>
                </td>
                <td style="float:right;width:50%;height:200px;">
                  <span class="errorWritePlaces" style="width:320px;display:block;height:160px;overflow:auto;">{{apiInfoVersion.callBackFailExample}}</span>
                </td>
              </tr>

              </tbody>
            </table>
          </div>
        </div>

        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="look = false"> </el-button>
        </div>
      </el-dialog>

      <div class="page">
        <div style="margin-top:20px;text-align:right;">
          <el-pagination @current-change="handleCurrentChange" :current-page="currentPage" :page-size="this.pageSize" layout="total, prev, pager, next" :total="total"></el-pagination>
        </div>
      </div>

    </div>
    <!-- API end -->
    <!--   start -->
    <el-dialog :model="slideDatas" title="API" :visible.sync="switchover" size="tiny">
      <p>
        <span class="textRed">{{slideDatas.env ==1 ?'':''}}</span>API
        <span class="textRed">{{slideDatas.apiName}}</span></p>
      <p>
        <span>{{slideDatas.productEnvVersion}}</span>
      </p>
      <p>
        <span>{{slideDatas.versionId}}</span>
      </p>

      <el-form :model="form">
        <el-form-item label="">
          <el-input style="width:100%" type="textarea" class="w350" :rows="4" v-model="slideDatas.pubDescription"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="switchover = false"> </el-button>
        <el-button type="primary" @click="switchDetail"> </el-button>
      </div>
    </el-dialog>
    <!--   end -->
  </div>
</template>

<script>
import axios from 'axios'
import { formatJson } from '../../assets/json'
import moment from 'moment'
import page from '@/components/common/page'
import menuListDetail from '@/components/common/menuListDetail'
export default {
  name: 'accreditMes',
  components: {
    page,
    menuListDetail
  },
  data() {
    return {
      // 
      paramLocationFomatter(row, column) {
        const local = row[column.property];
        if (local == undefined) {
          return "";
        }
        return local == 1 ? "header" : (local == 2 ? "query" : "body");
      },
      currentPage: 1,
      pageSize: 10,
      total: 100,
      apiInfoVersion: {},
      backendVersionsList: [],
      backendParamsList: [],
      constParamsList: [],
      requestParamsVersionsList: [],
      historyData: [],
      value: '',
      look: false,        //
      switchover: false, //
      slideDatas: {}, // 
      form: {
        name: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',

      },
      apiGroupId: '',
      apiId: '',
      formLabelWidth: '120px',
      amendApiIndex: 0,
      jurisdiction:{},
      vsionBtn:false,
    }
  },
  created() {
    this.historyInit();
    this.getUserPermissions();
  },
  methods: {
    //
    getUserPermissions(){
      axios({
        method: "post",
        url: "/getCasUser",
        data: {}
      }).then(res => {
        this.jurisdiction  = res.data;
      }).catch(error => {
        console.log("");
      });
    },
    //============================================  API   start ==============================================
    historyInit() {
      let apiGroupId = this.$route.query.apiGroupId;
      let id = this.$route.query.id;
      axios({
        method: 'post',
        url: '/api/apiInfoVersionsList',
        data: {
          apiGroupId: apiGroupId,
          apiId: id,
          pageNum: this.currentPage
        }
      }).then(res => {
        if(!this.jurisdiction.roles.includes('admin')){
          this.vsionBtn = false;
        }else{
          this.vsionBtn = true;
        }

        this.tablegroup = res.data.data.list;
        this.pageSize = res.data.data.pageSize;
        this.total = res.data.data.total;
        this.historyData = res.data.data.list;
        for(var i = 0; i<this.jurisdiction.perms.length; i++){
          if(this.jurisdiction.perms[i] == '58a4bd497f4c4bea91412a46257e2cda'){

          }else if(this.jurisdiction.perms[i] == 'aab79de971484d95927dd87d05053612'){
            this.vsionBtn = true;
          }
        }
      }).catch(error => {
        console.log('');
      });
    },
    //============================================  API    end  ==============================================
    //============================================  API   start ==============================================
    lookData(row) {
      this.apiInfoVersion = {};
      this.backendVersionsList = [];
      this.requestParamsVersionsList = [];
      this.backendParamsList = [];
      this.constParamsList = [];
      this.apiInfoVersion = row;
      let id = this.apiInfoVersion.id;
      let apiGroupId = this.$route.query.apiGroupId;
      axios({
        method: 'post',
        url: '/api/findApiInfoVersion',
        data: {
          apiGroupId: apiGroupId,
          id: id
        }
      }).then(res => {
        this.apiInfoVersion = res.data.data.apiInfoVersion;
        this.backendVersionsList = res.data.data.backendVersionsList;
        res.data.data.backendVersionsList.forEach((obj, index) => {
          if (obj.paramsType == 1) {
            this.backendParamsList.push(obj);
          } else if (obj.paramsType == 2) {
            this.constParamsList.push(obj);
          }
        });

        this.requestParamsVersionsList = res.data.data.requestParamsVersionsList;
        this.look = true;

        var resultJson = formatJson(this.apiInfoVersion.callBackSuccessExample);
        $(".writePlaces").html("<pre>" + resultJson + "<pre/>");
        var resultErrorJson = formatJson(this.apiInfoVersion.callBackFailExample);
        $(".errorWritePlaces").html("<pre>" + resultErrorJson + "<pre/>");
      }).catch(error => {
        console.log('');
      });
    },
    //============================================  API    end  ==============================================

    //============================================  API start ============================================
    switchoverData(row) {
      this.slideDatas = row;
      this.slideDatas.productEnvVersion = this.$route.query.productEnvVersion;
      this.switchover = true;
    },
    switchDetail() {
      let pubDescription = this.slideDatas.pubDescription;
      let id = this.slideDatas.id;
      let apiGroupId = this.$route.query.apiGroupId;
      axios({
        method: 'post',
        url: '/api/reswitchVersion',
        data: {
          id: id,
          apiGroupId: apiGroupId,
          pubDescription: pubDescription
        }
      }).then(res => {
        if (res.data.code !== 2000) {
          this.$message({
            message: res.data.message,
            type: 'error'
          });
        } else {
          this.switchover = false;
          this.$message({
            type: 'success',
            message: ''
          });
          this.historyInit();
        }
      }).catch(error => {
        console.log('');
      });
    },
    //============================================  API  end  ============================================
    //
    handleCurrentChange(val) {
      this.currentPage = val;
      this.historyInit();
    },
    //moment
    dateFormat(row, column) {
      const date = row[column.property];
      if (date == undefined) {
        return "";
      }
      return moment(date).format("YYYY-MM-DD HH:mm:ss");
    },
    moreStart(index) {
      $(".moreMes").show();
      this.amendApiIndex = index;
    },
    moreEnd(index) {
      $(".moreMes").hide();
      this.amendApiIndex = 0;
    },
    tableRowClassName(row, index) {
      if (index%2 !== 0) {
        return 'info-row';
      }
      return '';
    },
  }
}
</script>
<style scoped>
.accreditMes {
  background: #fff;
}

.moreMes {
  position: absolute;
  top: 0;
  right: 240px;
  display: none;
  width: 500px;
  background: #fff;
  border: 1px solid #ddd;
}

</style>



