import axios from 'axios'


export default {
  /**
   * 
   * @param component
   */
  getUserPermissions: (component) => {
    axios({
      method: "post",
      url: "/getCasUser",
      data: {}
    }).then(res => {
      component.jurisdiction = res.data;
      if (component.jurisdiction.perms.length === 0 || component.jurisdiction.perms == null) {
        component.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          location.href = res.data.logoutUrl;
        }).catch(() => {
          component.$message({
            type: 'info',
            message: ''
          });
        });
      }
    }).catch(error => {
      console.log(":" + error);
    });
  },

  /**
   * 
   * @param component
   */
  getBillingRules: (component) => {
    axios({
      method: 'post',
      url: ' /gateway/charge/billingRules/chargeNameList',
      data: {}
    }).then(res => {
      if (res.data.code === 2000) {
        component.billingRules = res.data.data;
      } else {
        component.$message({type: "info", message: res.data.message});
      }
    }).catch(error => {
      console.log(': ' + error);
    });
  },
  /**
   * 
   * @param row
   * @param index
   * @returns {string}
   */
  tableRowClassName(row, index) {
    if (index % 2 !== 0) {
      return 'info-row';
    }
    return '';
  },

  /**
   * API
   * @param component
   */
  getGroupAry: (component) => {
    axios({
      method: 'post',
      url: '/apiGroup/listOfNoLowerLevelGroup',
      data: {}
    }).then(res => {
      if (res.data.code === 2000) {
        component.groupAry = res.data.data
      } else {
        component.$message({type: "info", message: res.data.message});
      }
    }).catch(error => {
      console.log('API: ' + error);
    });
  },

  /**
   * groupIdAPI
   * @param component
   * @param groupId
   */
  getApiAry: (component, groupId) => {
    if (!groupId) return;
    axios({
      method: 'post',
      url: '/api/findApiByGroupId',
      data: {id: groupId}
    }).then(res => {
      if (res.data.code === 2000) {
        component.apiAry = res.data.data
      } else {
        component.$message({type: "info", message: res.data.message});
      }
    }).catch(error => {
      console.log('groupIdAPI: ' + error);
    });
  },
}



