<template>
  <div>
    <el-form :model="groupHerfId" ref="groupHerfId" class="demo-ruleForm">
      <span style="font-weight:700;">API</span>
      <div class="issueAPI">{{groupHerfId.apiName}}</div>

      <!--<p style="font-weight:700;"></p>-->

      <!--<el-radio-group v-model="env">-->
        <!--<el-radio-button label=1></el-radio-button>-->
        <!--<el-radio-button label=2></el-radio-button>-->
      <!--</el-radio-group>-->

      <el-form-item prop="pubDescription" label="" :rules="[{required:true,message:''},{validator:issueEmpty, trigger: 'blur' },{ min:1, max:180, message:' 1  180 ',trigger:'blur'}]">
        <el-input type="textarea" v-model="groupHerfId.pubDescription" :rows="4" placeholder="180" auto-complete="off"></el-input>
      </el-form-item>
      <p class="textRed">Mock </p>
      <p>API</p>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button class="cancelBtn" @click="$emit('cancle', '')"> </el-button>
      <el-button type="primary" class="addOne" @click="publishApi('groupHerfId')"></el-button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'publishApi',
  props: ["groupHerfId", "issue"],

  data() {

    return {
      env: 1,
      //
      issueEmpty: (rule, value, callback) => {
        var reg = /\s+/;
        if (reg.test(value)) {
          callback(new Error(''));
        } else {
          callback();
        }
      },

    }
  },
  created() {

  },
  methods: {

    publishApi(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const apiGroupId = this.groupHerfId.apiGroupId;
          const id = this.groupHerfId.id;
          const pubDescription = this.groupHerfId.pubDescription;

          axios({
            method: 'post',
            url: '/api/publish',
            data: {
              apiGroupId,
              id,
              env: this.env,
              pubDescription: pubDescription,
            }
          }).then(res => {
            this.$emit('issueOK', '')
            this.$message({
              type: 'success',
              message: ''
            });
            this.env = 1;
          }).catch(error => {
            console.log('');
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });

    },

  }
}
</script>

<style scoped>

</style>



