<template>
  <div>
    <el-form v-model="groupHerfId">
      <span style="font-weight:700;">API</span>
      <div class="issueAPI">{{groupHerfId.apiName}}</div>

      <!--<p style="font-weight:700;"></p>-->

      <!--<el-radio-group v-model="env">-->
        <!--<el-radio-button label=1></el-radio-button>-->
        <!--<el-radio-button label=2></el-radio-button>-->
      <!--</el-radio-group>-->

      <p style="font-weight:700;"></p>
      <el-form-item :label-width="120" :rules="[{required:true, message: '' ,trigger: 'blur' },
          { min: 1, max: 180, message: '', trigger: 'blur' },{ validator: descriptionValidate, trigger: 'blur'}]">
        <el-input placeholder="" class="w350" v-model='remarks'></el-input>
      </el-form-item>

      <p style="color: red">API</p>

    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button class="cancelBtn" @click="$emit('cancle', '')"> </el-button>
      <el-button type="primary" class="addOne" @click="overLineApi"></el-button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'overLineApiCom',
  props: ["groupHerfId"],

  data() {

    return {
      env: 1,
      remarks: ''
    }
  },
  created() {

  },
  methods: {

    overLineApi() {
      const apiGroupId = this.groupHerfId.apiGroupId;
      const id = this.groupHerfId.id;
      if (this.remarks.trim() === '') {
        this.$message({
          message: '!',
          type: 'warning'
        });
        return;
      }

      axios({
        method: 'post',
        url: '/api/offline',
        data: {
          apiGroupId: apiGroupId,
          id,
          env: this.env,
          remarks: this.remarks
        }
      }).then(res => {
        if (res.data.code !== 2000) {
          this.$message({
            message: ',,',
            type: 'warning'
          });
        } else {
          this.$emit('offLineOK', '')
          this.$message({
            type: 'success',
            message: ''
          });
        }
      }).catch(error => {
        console.log(error.message);
      });
    },
  }
}
</script>


<style scoped>

</style>



