<template>
  <div class="ipList" style="background:#ffffff;height:100%;">
    <el-form :inline="true" class="search_form">
      <el-form-item>
        <el-input placeholder="header" class="w350" v-model='searcHeader'></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="info" class="searchOne" @click="searchData()"> </el-button>
        <el-button type="primary addOne" v-if="addName" class="searchOne" @click="creatIp = true,titleText = ''"> </el-button>
      </el-form-item>
    </el-form>

    <el-table :data="tablegroup" style="width: 100%;border-top:8px solid #e6e6e6" v-loading="listLoading" :row-class-name="tableRowClassName">
      <el-table-column prop="" type="index" label="" width="80"></el-table-column>
      <el-table-column prop="headerName" label=""></el-table-column>
      <el-table-column prop="headerValue" label="" width="400"></el-table-column>
      <el-table-column prop="headerLength" label=""></el-table-column>
      <el-table-column prop="createTime" label="" :formatter="dateFormat"></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <a href="javascript:void(0)" class="exportBtn">
            <el-button type="text" v-if="amendName" class="radiusBtn pubColor" size="small" @click="amendIp(scope.row)"></el-button>
            <el-button type="text" v-if="delName" class="radiusBtn pubColor" size="small" @click="delIp(scope.row)"></el-button>
          </a>
        </template>
      </el-table-column>
    </el-table>
    <div class="teCen mt60" v-if="this.tablegroup.length < 1">
      <p><img src="../../assets/Shape.png" /></p>
      <span></span>
    </div>

    <div class="page" style="padding-bottom:30px;" >
      <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
        :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>

    <!--  start -->
    <el-dialog :title="titleText" :visible.sync="creatIp" size="tiny" style="top:-5%">
        <el-form :model="addGroupData" ref="addGroupData" class="demo-ruleForm">

          <el-form-item label="" prop="name" :label-width="formLabelWidth" :rules="[{required:true, message: ''}]">
            <el-input class="w9000" v-model="addGroupData.name"></el-input>
          </el-form-item>

          <el-form-item label="" prop="val" :label-width="formLabelWidth" :rules="[{required:true, message: ''}]">
            <el-input class="w9000" v-model="addGroupData.val"></el-input>
          </el-form-item>

          <el-form-item label="" prop="lenght" :label-width="formLabelWidth" :rules="[{required:true, message: ''},
          {type:'number', message: ''}]">
            <el-input class="w9000" v-model.number="addGroupData.lenght"></el-input>
          </el-form-item>

          <el-form-item label="" prop="user" :label-width="formLabelWidth" :rules="[{required:true, message: ''}]">
              <el-input class="w9000" v-if="titleText == ''" v-model="addGroupData.user"></el-input>
            <el-input class="w9000" v-if="titleText == ''" disabled v-model="addGroupData.user"></el-input>
          </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button class="cancelBtn" @click="creatIp = false"> </el-button>
          <el-button type="primary" class="addOne" @click="addIp('addGroupData')"> </el-button>
        </div>
      </el-dialog>


  </div>
</template>

<script>
import axios from 'axios'
import moment from 'moment'

export default {
  name: 'findApi',
  components: {

  },
  data() {
    return {
      jurisdiction:{},
      addGroupData:{},
      amendObj:{},
      index:0,
      titleText:'',
      creatIp:false,
      addName:true,
      amendName:true,
      delName:true,
      pageNum: 1,
      pageSize: 10,
      total: 0,
      page: 1,
      searcHeader:'',
      listLoading: false,
      tablegroup: [],
      newPage:'',
      formLabelWidth: "120px",
      companyId:'',
      delValData:{},

      dateFormat(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss");
      }
    }
  },

  created() {
    //this.getUserPermissions();
    this. initData();
  },

  methods: {

    //
    getUserPermissions(){
      axios({
        method: "post",
        url: "/getCasUser",
        data: {}
      }).then(res => {
        this.jurisdiction  = res.data;
        // if(this.jurisdiction.roles[0] !== 'admin'){
        //   this.addName = false;
        //   this.amendName = false;
        //   this.delName = false;
        // }else{
        //   this.addName = true;
        //   this.amendName = true;
        //   this.delName = true;
        // }
      }).catch(error => {
        console.log("");
      });
    },
    //
    initData() {
      axios({
        method: 'post',
        url: '/api/tcpHeaderList',
        data: {
          pageSize: this.newPage == '' ? 10 : this.newPage,
          pageNum: this.pageNum
        }

      }).then(res => {
        this.tablegroup = res.data.data.list;
        this.pageNum = res.data.data.pageNum;
        this.pageSize = res.data.data.pageSize;
        this.total = res.data.data.total;
      }).catch(error => {
        console.log('');
      });
    },
    //
    searchData() {
      axios({
        method: 'post',
        url: '/api/searchTcpHeaderList',
        data: {
          header:this.searcHeader == '' ? this.initData() :this.searcHeader,
          pageSize: this.newPage == '' ? 10 : this.newPage,
          pageNum: this.pageNum
        }

      }).then(res => {
        this.tablegroup = res.data.data.list;
        this.pageNum = res.data.data.pageNum;
        this.pageSize = res.data.data.pageSize;
        this.total = res.data.data.total;
      }).catch(error => {
        console.log('');
      });
    },
    //
    addIp(formName){
      this.$refs[formName].validate(valid => {
      if (valid) {
        axios({
          method: 'post',
          url: this.titleText == '' ? '/api/addTcpHeader' : '/api/updateTcpHeader',
          data: {
            headerName:this.addGroupData.name,
            headerValue:this.addGroupData.val,
            headerLength:this.addGroupData.lenght,
            creatUser:this.addGroupData.user,
            id:this.amendObj.id
          }

        }).then(res => {
          if(res.data.code == 2000){
            this.creatIp = false;
            this.initData();
            this.addGroupData = {};
            this.$refs[formName].resetFields();
          }else{
            this.$message({type: "info",message: res.data.message});
          }
        }).catch(error => {
          console.log('');
        });
      } else {
        console.log("error submit!!");
        return false;
      }
    });
    },
    //
    amendIp(row){
      this.amendObj = row;
      this.creatIp = true;
      this.titleText = '';
      this.addGroupData.name = row.headerName;
      this.addGroupData.val = row.headerValue;
      this.addGroupData.lenght = row.headerLength;
      this.addGroupData.user = row.creatUser;
    },

    //
    delIp(row){
      this.delValData = row;
      this.$confirm(`header "${this.delValData.headerName}",!`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
          this.delValList();
      }).catch(() => {
        this.$message({
          type: "info",
          message: ""
        });
      });
    },

    delValList(){
      axios({
        method: 'post',
        url: '/api/deleteTcpHeader',
        data: {
          id:this.delValData.id
        }

      }).then(res => {
        if(res.data.code == 2000){
          this.$message({type: "success",message: 'header'});
          this.initData();
        }else{
          this.$message({type: "info",message: res.data.message});
        }
      }).catch(error => {
        console.log('');
      });
    },


    tableRowClassName(row, index) {
      if (index%2 !== 0) {
        return 'info-row';
      }
      return '';
    },

    //
    handleCurrentChange(val) {
      this.pageNum = val;
      this.initData();


    },
    handleSizeChange(val) {
      this.pageNum = val;
      this.initData();
    }



  }

}
</script>

<style scoped>

</style>



