<template>
  <div class="appListDetail" style="background:white;">

    <div class="detailBtn" style="height:50px;">
      <div class="fl">
        <span class="detailNav"></span>
        <router-link to="/appList">
          <el-button size="small" style="width:auto"></el-button>
        </router-link>
      </div>
      <el-button type='primary' size="small" class="fr" @click="amendApp = true;"></el-button>
      <p style="clear:both"></p>
    </div>

    <div class="panel panel-default">
      <div class="console-panel-header">
        <span class="panel-header"></span>
        <table v-model="applyDataDetail" class="table table-hover table-bordered">
          <tbody>
            <tr>
              <td>
                <span>{{applyDataDetail.appName}}</span>
              </td>
              <td>ID
                <span>{{applyDataDetail.id}}</span>
              </td>
            </tr>

            <tr>
              <td>appKey
                <span>{{applyDataDetail.appKey}}</span>
              </td>
              <td>appSecret
                <span>{{applyDataDetail.appSecret}}</span>
              </td>
            </tr>

            <tr>
              <td>
                <span>{{applyDataDetail.limitStrategyuuid}}</span>
              </td>
              <td>
                <span>{{applyDataDetail.description}}</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <el-tabs type="border-card">
      <el-tab-pane label="API">
        <el-button type='primary' size="small" class="fr" @click="authApiOpen()"></el-button>
        <p style="clear:both"></p>
        <el-table :data="granted" border style="width: 100%">
          <el-table-column prop="apiName" label="API"></el-table-column>
          <el-table-column prop="apiGroupName" label=""></el-table-column>
          <el-table-column prop="apiDescription" label=""></el-table-column>
          <el-table-column prop="env" label="" :formatter='envFormat'></el-table-column>
          <el-table-column prop="updateTime" label="" :formatter="dateFormat"></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="deleteApi(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="page">
          <div class="block fr" style="margin-top:20px;">
            <el-pagination @current-change="handleCurrentChange" :current-page="pageNum" :page-size="this.pageSize" layout="total, prev, pager, next" :total="total"></el-pagination>
          </div>
        </div>
      </el-tab-pane>

      <el-tab-pane label="">
        <el-button type='primary' id='addOver' size="small" class="fr" @click="addStrategy"></el-button>
        <p style="clear:both"></p>
        <el-table :data='strategyData' border style="width: 100%">
          <el-table-column prop='name' label=""></el-table-column>
          <el-table-column prop="unit" label="" :formatter='unitFormat'></el-table-column>
          <el-table-column prop="no" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="removeStrategy()"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>

    <!--  start -->
    <el-dialog title="" :visible.sync="bindStrategy" size="small" id="accreditBox" @close='closeDialog'>
      <div class="accreditLine"></div>
      <el-form>
        <div>
          <span style="font-weight:700;"></span>
          <p></p>
          <div class="fl flBox" style="margin-bottom:20px;width: 100%">
            <el-form :inline="true" class="demo-form-inline">
              <el-form-item id="accreditInput">
                <el-input placeholder="" v-model='strategyName'></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="getStrategyList()"></el-button>
              </el-form-item>
            </el-form>

            <el-table ref="multipleTable" :data="allStrategy" border tooltip-effect="dark" style="width: 100%;height: 380px;overflow:auto">

              <el-table-column label="" width="40">
                <template slot-scope="scope">
                  <el-radio v-model="radio" @change.native="getCurrentRow(scope.$index)"></el-radio>
                </template>
              </el-table-column>

              <el-table-column label="">
                <template slot-scope="scope">{{ scope.row.name}}</template>
              </el-table-column>
              <el-table-column prop="unit" label="" :formatter='unitFormat'></el-table-column>
              <el-table-column prop="no" label=""></el-table-column>
              <el-table-column prop="createTime" label="" :formatter='dateFormat'></el-table-column>
            </el-table>
          </div>
        </div>
      </el-form>
      <p style="clear:both"></p>
      <div slot="footer" class="dialog-footer">
        <el-button @click="bindStrategy = false"> </el-button>
        <el-button type="primary" @click="setAuth()"> </el-button>
      </div>
    </el-dialog>
    <!--  end -->

    <!--  start -->
    <el-dialog title="API" :visible.sync="authApi" size="small" id="accreditBox" @close='closeAuthApi'>
      <div class="accreditLine"></div>
      <el-form>

        <span style="font-weight:700;"></span><br><br>
        <template>
          <el-radio-group v-model="radioEnv" @change='radioChange'>
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
          </el-radio-group>
        </template><br>

        <div>
          <span style="font-weight:700;">API</span>
          <p></p>
          <div class="fl flBox" style="margin-bottom:20px; width:100%">
            <el-form :inline="true" class="demo-form-inline">
              <el-form-item id="accreditInput">
                <el-input placeholder="API" v-model='searchApiName'></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click='initApiList()'></el-button>
              </el-form-item>
            </el-form>

            <el-table ref="multipleTable" :data="apiList" border tooltip-effect="dark" style="width:100%;height:380px;overflow:auto" @selection-change="handleSelectionChangeApi">
              <el-table-column type="selection" width="55" class="selection" prop='limitStrategyuuid' @selection-change="handleSelectionChangeApi">
              </el-table-column>

              <el-table-column prop="apiName" label="api"></el-table-column>
              <el-table-column prop="apiGroupName" label=""></el-table-column>
              <el-table-column prop="createTime" label="" :formatter='dateFormat'></el-table-column>
            </el-table>

          </div>
        </div>
      </el-form>
      <p style="clear:both"></p>
      <div slot="footer" class="dialog-footer">
        <el-button @click="authApi = false"> </el-button>
        <el-button type="primary" @click="confirmAuthApi()"> </el-button>
      </div>
    </el-dialog>
    <!-- API end -->

    <!--  begin -->
    <el-dialog title="" :visible.sync="amendApp" size="tiny" @close='closeModify'>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" :label-width="formLabelWidth" prop='modifyAppName'>
          <el-input v-model='ruleForm.modifyAppName'></el-input>
          <p style="line-height:20px;">4~15</p>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop='modifyDescription'>
          <el-input v-model='ruleForm.modifyDescription' type="textarea" :rows="4" placeholder="180"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="amendApp  = false"> </el-button>
        <el-button type="primary" @click="modifyApp('ruleForm')"> </el-button>
      </div>
    </el-dialog>
    <!--  end -->

  </div>
</template>

<script>
import axios from 'axios'
import moment from 'moment'
export default {
  name: 'appListDetail',
  components: {
  },
  data() {
    //------------------------------------//
    let appName1 = (rule, value, callback) => {
      let regExp = /^[\u4e00-\u9fa5a-zA-Z][\u4e00-\u9fa5a-zA-Z\d](?!_)(?!.*?_$)[a-zA-Z0-9_\u4e00-\u9fa5]+$/;
      if (regExp.test(value) === false) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    //------------------appName------------------//
    let appName2 = (rule, value, callback) => {
      if (value != this.applyDataDetail.appName) {
        axios({
          method: 'post',
          url: '/app/checkName',
          data: {
            appName: value
          }
        }).then(res => {
          if (res.data.data === false) {
            callback(new Error(''));
          } else {
            callback();
          }
        }).catch(error => {
          console.log('');
        });
      } else {
        callback();
      }
    };
    return {
      pageNum: 1,
      pageSize: 8,
      total: 2,
      value: '',
      applyDataDetail: {},                  //APP
      createAccredit: false,                //API
      bindStrategy: false,
      granted: [],  //API
      amendApp: false,                  // 
      delGroupData: {},                 //
      strategyData: [],
      // modifyAppName:'',
      // modifyDescription:'',
      allStrategy: [],
      strategyName: '',
      radio: '',
      strategyUuid: '',
      formLabelWidth: '120px',
      authApi: false,
      searchApiName: '',
      env: 1,
      multipleSelection: [],
      apiList: [],
      radioEnv: '',
      envFormat(row, column) {
        const env = row[column.property];
        if (env == undefined) {
          return "";
        }
        return env == 1 ? "" : "";
      },
      dateFormat(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss");
      },
      unitFormat(row, column) {
        const unit = row[column.property];
        if (unit == undefined) {
          return "";
        }
        return unit == 1 ? "" : (unit == 2 ? "" : "");
      },
      ruleForm: {
        modifyAppName: '',
        modifyDescription: ''
      },
      rules: {
        modifyAppName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 15, message: ' 4  15 ', trigger: 'blur' },
          { validator: appName1, trigger: 'blur' },
          { validator: appName2, trigger: 'blur' }
        ],
        modifyDescription: [
          { min: 0, max: 180, message: '180', trigger: 'blur' }
        ]
      }

    }
  },

  created() {
    this.appDetails();
    this.findApis();
  },

  methods: {
    //===========================================      List   start=========================================
    appDetails() {
      let id = this.$route.query.id;
      axios({
        method: 'post',
        url: '/app/details',
        data: {
          id: id
        }
      }).then(res => {
        this.applyDataDetail = res.data.data;
        this.ruleForm.modifyAppName = res.data.data.appName;
        this.ruleForm.modifyDescription = res.data.data.description;
        if (res.data.data.strategy != null) {
          this.strategyData.push(res.data.data.strategy);
        }
      }).catch(error => {
        console.log('');
      });
    },
    //===========================================      List   end===========================================

    //===========================================         API         start  ===========================================
    findApis() {
      let appId = this.$route.query.id;
      axios({
        method: 'post',
        url: '/app/findApis',
        data: {
          appId: appId,
          pageNum: this.pageNum,
          pageSize: 8
        }
      }).then(res => {
        this.granted = res.data.data.elements;
        this.pageNum = res.data.data.pageNum == 0 ? 1 : res.data.data.pageNum;
        this.pageSize = res.data.data.pageSize;
        this.total = res.data.data.count;
      }).catch(error => {
        console.log('');
      });
    },
    //bar
    handleCurrentChange(val) {
      this.pageNum = val;
      this.findApis();
    },
    //===========================================         API          end   ===========================================
    //api
    deleteApi(val) {
      this.$confirm('API, ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        axios({
          method: 'post',
          url: '/app/unAccredit',
          data: {
            apiId: val.id,
            appCertificationId: this.applyDataDetail.id,
            env: val.env
          }
        }).then(res => {
          this.$message({
            type: 'success',
            message: '!'
          });
          this.strategyData = [];
          this.appDetails();
          this.findApis();
        }).catch(error => {
          console.log('');
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    //
    removeStrategy() {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        axios({
          method: 'post',
          url: '/app/updateUuid',
          data: {
            id: this.applyDataDetail.id,
            limitStrategyuuid: null
          }
        }).then(res => {
          this.$message({
            type: 'success',
            message: '!'
          });
          this.strategyData = [];
          this.appDetails();
          this.findApis();
        }).catch(error => {
          console.log('');
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    //
    closeModify() {
      this.ruleForm.modifyAppName = this.applyDataDetail.appName;
      this.ruleForm.modifyDescription = this.applyDataDetail.description;
    },
    //app
    modifyApp(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          axios({
            method: 'post',
            url: '/app/update',
            data: {
              id: this.applyDataDetail.id,
              appName: this.ruleForm.modifyAppName == '' ? null : this.ruleForm.modifyAppName,
              description: this.ruleForm.modifyDescription == '' ? null : this.ruleForm.modifyDescription
            }
          }).then(res => {
            this.strategyData = [];
            this.amendApp = false;
            this.appDetails();
            this.findApis();
          }).catch(error => {
            console.log('');
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    //
    getAllStrategy(val) {
      axios({
        method: 'post',
        url: '/strategy/getAllStrategy',
        data: {
          strategyName: val
        }
      }).then(res => {
        this.allStrategy = res.data.data;
      }).catch(error => {
        console.log('');
      });
    },
    //
    addStrategy() {
      if (this.strategyData.length != 0) {
        this.$message({
          message: '',
          type: 'warning'
        });
      } else {
        this.bindStrategy = true;
        this.getAllStrategy(null);
      }
    },
    //
    getStrategyList() {
      this.getAllStrategy(this.strategyName == '' ? null : this.strategyName);
    },
    //
    closeDialog() {
      this.strategyName = '';
    },
    //
    getCurrentRow(index) {
      this.strategyUuid = this.allStrategy[index].uuid;
    },
    //
    setAuth() {
      axios({
        method: 'post',
        url: '/app/updateUuid',
        data: {
          id: this.applyDataDetail.id,
          limitStrategyuuid: this.strategyUuid
        }
      }).then(res => {
        this.bindStrategy = false;
        this.strategyData = [];
        this.findApis();
        this.appDetails();
      }).catch(error => {
        console.log('');
      });
    },
    //API
    handleSelectionChangeApi(val) {
      this.multipleSelection = val;
    },
    //
    closeAuthApi() {
      this.searchApiName = '';
    },
    //API
    authApiOpen() {
      this.authApi = true;
      this.initApiList();
    },
    initApiList() {
      axios({
        method: 'post',
        url: '/app/findNoAccreditApis',
        data: {
          appId: this.applyDataDetail.id,
          apiName: this.searchApiName == '' ? null : this.searchApiName,
          env: this.env
        }
      }).then(res => {
        this.apiList = res.data.data;
      }).catch(error => {
        console.log('');
      });
    },
    ///
    radioChange(val) {
      if (val == '') {
        this.env = 1;
        this.multipleSelection = [];
        this.initApiList();
      } else if (val == '') {
        this.env = 2;
        this.multipleSelection = [];
        this.initApiList();
      }
    },
    //api
    confirmAuthApi() {
      var flag = 0;
      var apiIds = '';
      if (this.multipleSelection.length != 0) {
        this.multipleSelection.forEach(row => {
          if (flag == 0) {
            apiIds += row.id;
          } else {
            apiIds += (',' + row.id);
          }
          flag++;
        });
        axios({
          method: 'post',
          url: '/app/appToApi',
          data: {
            appId: this.applyDataDetail.id,
            apiIds: apiIds,
            env: this.env
          }
        }).then(res => {
          this.authApi = false;
          this.findApis();
        }).catch(error => {
          console.log('');
        });
      }
    }
  }
}
</script>
<style scope>
.el-table__header-wrapper .el-table__header {
  width: 100%!important;
}

.el-table__body-wrapper table {
  width: 100%!important;
}
</style>



