<template>
  <div class="port" style="background:#ffffff;position:relative;">

    <el-button class="createApi_btn" type="primary" @click="creatGroup=true"></el-button>
    <el-form :inline="true" class="search_form">
      <el-form-item>
        <el-input v-model="searchGroupName" placeholder="" class="w350"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="created()"></el-button>
      </el-form-item>
    </el-form>
    <el-table :data="applyData" border style="width: 100%">
      <el-table-column prop="appName" label="">
        <template slot-scope="scope">
          <router-link :to="{path:'appListDetail',query:{id:scope.row.id}}">{{scope.row.appName}}</router-link>
        </template>
      </el-table-column>
      <el-table-column prop="appKey" label="appKey"></el-table-column>
      <el-table-column prop="description" label=""></el-table-column>
      <el-table-column prop="createTime" label="" :formatter='dateFormat'></el-table-column>
      <el-table-column label="" width="150">
        <template slot-scope="scope">
          <el-button type="text" @click="openModify(scope.row)"></el-button>
          <el-button type="text" @click="removeApp(scope.row.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="page" style="padding-bottom:30px;">
      <el-pagination style="text-align: right;margin-top:80px;" @current-change="handleCurrentChange" :current-page="pageNum" :page-size="this.pageSize" layout="total, prev, pager, next" :total="total"></el-pagination>
    </div>

    <!--  start -->
    <el-dialog title="" :visible.sync="creatGroup" size="tiny" @close='closeCreate'>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

        <el-form-item label="" :label-width="formLabelWidth" prop='appName'>
          <el-input class="w9000" v-model='ruleForm.appName'></el-input>
          <p style="line-height:20px;">4~15</p>
        </el-form-item>

        <el-form-item label="APPkey" :label-width="formLabelWidth" prop='description'>
          <el-input type="textarea" class="w9000" :rows="4" v-model='ruleForm.description'></el-input>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth" prop='appKey'>
          <el-input type="textarea" class="w9000" :rows="4" placeholder="180" v-model='ruleForm.appKey'></el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="creatGroup  = false"> </el-button>
        <el-button type="primary" @click="creatApp('ruleForm')"> </el-button>
      </div>
    </el-dialog>
    <!--  end -->

    <!-- APP start -->
    <el-dialog title="" :visible.sync="amendApp" size="tiny" @close='closeModify'>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" :label-width="formLabelWidth" prop='modifyAppName'>
          <el-input v-model='ruleForm.modifyAppName'></el-input>
          <p style="line-height:20px;">4~15</p>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop='modifyDescription'>
          <el-input v-model='ruleForm.modifyDescription' type="textarea" :rows="4" placeholder="180"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="amendApp  = false"> </el-button>
        <el-button type="primary" @click="modifyApp('ruleForm')"> </el-button>
      </div>
    </el-dialog>
    <!-- APP end -->

    <!--  start -->
    <el-dialog title="" :visible.sync="delApp" size="tiny">
      <el-form>
        <p>APPAPPAPPAPP</p>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="delApp  = false"> </el-button>
        <el-button type="primary" @click="delApp  = true"> </el-button>
      </div>
    </el-dialog>
    <!--  end -->
  </div>
</template>

<script>
import axios from 'axios'
import moment from 'moment'

export default {
  name: 'port',
  components: {

  },
  data() {
    //------------------------------------//
    let appName1 = (rule, value, callback) => {
      let regExp = /^[\u4e00-\u9fa5a-zA-Z][\u4e00-\u9fa5a-zA-Z\d](?!_)(?!.*?_$)[a-zA-Z0-9_\u4e00-\u9fa5]+$/;
      if (regExp.test(value) === false) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    //------------------appName------------------//
    let appName2 = (rule, value, callback) => {
      if (value != this.modifyBasicAppName) {
        axios({
          method: 'post',
          url: '/app/checkName',
          data: {
            appName: value
          }
        }).then(res => {
          if (res.data.data === false) {
            callback(new Error(''));
          } else {
            callback();
          }
        }).catch(error => {
          console.log('');
        });
      } else {
        callback();
      }
    };
    return {
      pageNum: 1,
      pageSize: 10,
      total: 5,
      applyData: [],
      group: '',
      formLabelWidth: '120px',
      creatGroup: false, //
      delGroup: false,
      // appName:'',
      // description:'',
      modify: '',
      // modifyAppName:'',
      // modifyDescription:'',
      modifyBasicAppName: '',
      amendApp: false, //App
      delApp: false, // App
      searchGroupName: "",//
      dateFormat(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss");
      },
      ruleForm: {
        appName: '',
        description: '',
        modifyAppName: '',
        modifyDescription: ''
      },
      rules: {
        appName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 15, message: ' 4  15 ', trigger: 'blur' },
          { validator: appName1, trigger: 'blur' },
          { validator: appName2, trigger: 'blur' }
        ],
        description: [
          { min: 0, max: 180, message: '180', trigger: 'blur' }
        ],
        modifyAppName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 15, message: ' 4  15 ', trigger: 'blur' },
          { validator: appName1, trigger: 'blur' },
          { validator: appName2, trigger: 'blur' }
        ],
        modifyDescription: [
          { min: 0, max: 180, message: '180', trigger: 'blur' }
        ]
      }

    }
  },
  created() {
    this.created();
  },
  methods: {
    //
    created() {
      axios({
        method: 'post',
        url: '/app/index',
        data: {
          appName: this.searchGroupName == '' ? null : this.searchGroupName,
          pageSize: 10,
          pageNum: this.pageNum
        }
      }).then(res => {
        this.applyData = res.data.data.elements;
        this.pageNum = res.data.data.pageNum == 0 ? 1 : res.data.data.pageNum;
        this.pageSize = res.data.data.pageSize;
        this.total = res.data.data.count;
      }).catch(error => {
        console.log('');
      });
    },
    //bar
    handleCurrentChange(val) {
      this.pageNum = val;
      this.created();
    },
    //
    closeCreate() {
      this.ruleForm.appName = '';
      this.ruleForm.description = '';
    },
    //app
    creatApp(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          axios({
            method: 'post',
            url: '/app/add',
            data: {
              appName: this.ruleForm.appName == '' ? null : this.ruleForm.appName,
              description: this.ruleForm.description == '' ? null : this.ruleForm.description
            }
          }).then(res => {
            this.creatGroup = false;
            this.created();
          }).catch(error => {
            console.log('');
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    //
    openModify(val) {
      this.modify = val;
      this.ruleForm.modifyAppName = val.appName;
      this.ruleForm.modifyDescription = val.description;
      this.modifyBasicAppName = val.appName;
      this.amendApp = true;
    },
    //app
    modifyApp(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          axios({
            method: 'post',
            url: '/app/update',
            data: {
              id: this.modify.id,
              appName: this.ruleForm.modifyAppName == '' ? null : this.ruleForm.modifyAppName,
              description: this.ruleForm.modifyDescription == '' ? null : this.ruleForm.modifyDescription
            }
          }).then(res => {
            this.amendApp = false;
            this.created();
          }).catch(error => {
            console.log('');
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    //
    closeModify() {
      this.ruleForm.modifyAppName = this.modify.appName;
      this.ruleForm.modifyDescription = this.modify.description;
    },
    //App
    removeApp(id) {
      this.$confirm('app, ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        axios({
          method: 'post',
          url: '/app/delete',
          data: {
            id: id
          }
        }).then(res => {
          this.$message({
            type: 'success',
            message: '!'
          });
          this.created();
        }).catch(error => {
          console.log('');
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.createLabel {
  text-align: right;
}

.must {
  color: red;
  display: none;
}
</style>



