<template>
  <div class="port" style="background:#fff;position:relative;;height:100%;">
    <div style="position:relative">
      <div class="treeBox">
        <span class="treeGroup"><span class="ml10"></span><el-button type="text" class="mr20 fr" style="color:#FEBD49" icon="plus" @click="addNext(null)"></el-button></span>
        <!-- <el-tree :data="dataTree" default-expand-all highlight-current :props="defaultProps" @node-click="handleNodeClick"></el-tree> -->

        <el-tree :data="alertTextArr" node-key="id"  :default-expanded-keys="defaultExpanded" :highlight-current="true" :expand-on-click-node="false" :props="defaultProps" @node-click="handleNodeClick"></el-tree>


      </div>
      <el-table :data="tablegroup" style="width:85%;float:right;" :row-class-name="tableRowClassName">
        <el-table-column prop="groupName" label="">
        </el-table-column>
        <el-table-column prop="groupDescription" label=""></el-table-column>
        <el-table-column prop="createTime" label="" :formatter="dateFormat"></el-table-column>
        <el-table-column label="" width="230">
          <template slot-scope="scope">
            <el-button type="text" class="radiusBtn addWcolor" size="small" v-if="addBtnP" @click="addNext(scope.row.parentId)"></el-button>
            <el-button type="text" class="radiusBtn addWcolor" size="small" v-if="addBtnP" @click="addNext(scope.row.id)"></el-button>
            <el-button type="text" class="radiusBtn amendColor" size="small" v-if="amendBtn" @click="amendGroup(scope.row)"></el-button>
            <el-button type="text" class="radiusBtn delColor" size="small" v-if="delBtn" @click="showDeleteModel(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="teCen" v-if="this.tablegroup.length < 1" style="position:absolute;left:50%;margin-top:60px;">
        <p><img src="../../assets/Shape.png" /></p>
        <span></span>
      </div>

    </div>
    <p style="clear:both"></p>
    <div class="page" style="padding-bottom:30px;" v-if="this.tablegroup.length > 0">
      <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage"
        :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>

    <!--  start -->
    <el-dialog title="" :visible.sync="creatGroup" size="tiny" :before-close='closeDialog' style="top:-5%">
      <el-form :model="addGroupData" ref="addGroupData" class="demo-ruleForm">
        <el-form-item label="" prop="groupName" :label-width="formLabelWidth" :rules="[{ required:true, message: ''},
        { validator: groupNamerule,trigger:'blur'},{ min: 2, max: 15, message: ' 2  15 ', trigger: 'blur' }]">
          <el-input class="w9000" v-model="addGroupData.groupName"></el-input>
          <p style="line-height:20px;">2~15</p>
        </el-form-item>

        <el-form-item label="" prop="groupDescription" :label-width="formLabelWidth" :rules="[{required:true, message: '' ,trigger: 'blur' },
          { min: 1, max: 180, message: ' 1  180 ', trigger: 'blur' },{ validator: descriptionValidate, trigger: 'blur'}]">
          <el-input type="textarea" v-model="addGroupData.groupDescription" class="w9000" :rows="4" placeholder="180" auto-complete="off"></el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="callOf('addGroupData')"> </el-button>
        <el-button type="primary" class="addOne" @click="addgroupList('addGroupData')"> </el-button>
      </div>
    </el-dialog>
    <!--  end -->

    <!--  start-->
    <el-dialog title="" :visible.sync="amendGroupDetail" size="tiny" style="top:-5%">
      <el-form :model="amnedData" ref="amnedData" class="demo-ruleForm">
        <el-form-item label="" prop="groupName" :label-width="formLabelWidth" :rules="[{ required:true, message: ''},
           { min: 2, max: 15, message: ' 2  15 ', trigger: 'blur' },{ validator: groupNames2, trigger: 'blur' },{ validator: groupNamerule,trigger:'blur'}]">
          <el-input class="w9000" v-model="amnedData.groupName"></el-input>
          <p style="line-height:20px;">2~15</p>
        </el-form-item>

        <el-form-item label="" prop="groupDescription" :label-width="formLabelWidth" :rules="[{required:true, message: '' ,trigger: 'blur' },
          { min: 1, max: 180, message: ' 1  180 ', trigger: 'blur' },{ validator: descriptionValidate, trigger: 'blur'}]">
          <el-input type="textarea" class="w9000" :rows="4" v-model="amnedData.groupDescription" placeholder="180"></el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="amendGroupDetail  = false"> </el-button>
        <el-button type="primary" class="addOne" @click="amendData('amnedData')"> </el-button>
      </div>
    </el-dialog>
    <!--   end-->

  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";
import apiTree from "@/components/common/apiTree";

export default {
  name: "port",
  components: {
    apiTree
  },
  data() {
    return {
      defaultExpanded: [],
      jurisdiction:{},
      temporary:'',
      delBtn:true,
      addBtnP:true,
      amendBtn:true,
      dataTree: [],
      defaultProps: {
        children: "children",
        label: "label"
      },
      alertTextArr:[],

      groupNamerule: (rule, value, callback) => {
        var reg = /\s+/;
        if (value === "") {
          callback(new Error(""));
        } else if (reg.test(value)) {
          callback(new Error(""));
        } else {
          callback();
        }
      },

      descriptionValidate: (rule, value, callback) => {
        var reg = /\s+/;
        if (value === "") {
          callback(new Error(""));
        } else if (reg.test(value)) {
          callback(new Error(""));
        } else {
          callback();
        }
      },

      groupNames: (rule, value, callback) => {
        if ((value = this.addGroupData.groupName)) {
          const groupName = this.addGroupData.groupName;
          axios({
            method: "post",
            url: "/apiGroup/checkUnique",
            data: {
              groupName: groupName
            }
          }).then(res => {
            if (res.data.data === false) {
              callback(new Error(""));
            } else {
              callback();
            }
          }).catch(error => {
            alert("");
          });
        } else {
          callback();
        }
      },
      groupNames2: (rule, value, callback) => {
        console.log(this.rowArr);
        if ((value = this.amnedData.groupName)) {
          var groupName = this.amnedData.groupName;
          var groupUUID = this.rowArr.groupUUID;
          axios({
            method: "post",
            url: "/apiGroup/checkUnique",
            data: {
              groupName: groupName,
              groupUUID:groupUUID
            }
          }).then(res => {
            if (res.data.data === false) {
              callback(new Error(""));
            } else {
              callback();
            }
          }).catch(error => {
            alert("");
          });
        } else {
          callback();
        }
      },

      currentPage: 1,
      pageSize: 10,
      total: 0,
      filters: {
        name: ""
      },
      page: 1,
      tablegroup: [],
      search: "",
      addGroupData: { groupName: "", groupDescription: "" }, //
      delGroupData: { groupName: "", groupDescription: "" }, //(v-model)
      amnedData: {
        groupName: "",
        groupDescription: ""
      },
      group: "",
      formLabelWidth: "120px",
      creatGroup: false, //
      description: "",
      searchGroupName: "", //
      amendGroupDetail: false,
      rowArr: {},
      addparentId: {
        groupName: "",
        groupDescription: ""
      },
      dataId:'',
      newPage:'',
    };
  },

  created() {
    // this.initData();
    this.treeGroup();

    this.getUserPermissions();
    this.treeList();
  },

  methods: {
    getUserPermissions(){
      axios({
        method: "post",
        url: "/getCasUser",
        data: {}
      }).then(res => {
        this.jurisdiction  = res.data;
        if(this.jurisdiction.perms.length == 0 || this.jurisdiction.perms == null){
          this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            location.href = res.data.logoutUrl;
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        }
      }).catch(error => {
        console.log("");
      });
    },


    addNext(parentId) {
      this.creatGroup = true;
      this.addGroupData.parentId = parentId;
    },

    treeGroup() {
      axios({
        method: "post",
        url: "/apiGroup/listByLevelForGroupUI",
        data: {}
      }).then(res => {
        if(res.data.code == 403){
          location.href = res.data.data;
        }

          this.dataTree = res.data.data;
          // 
          var data = res.data.data,
            iLen = data.length,
            results = [];
          for (var i = 0; i < iLen; i++) {
            if (data[i].parentId == null) {
              var child = {
                label: data[i].groupName,
                id: data[i].id
              };
              child.children = this.aa(data[i].id, data);
              results.push(child);
            }
          }

          results.splice(0,0,{id: 0,label: ""});
        this.defaultExpanded = []
        results.forEach(item => {
          if(item.id && item.id !== 0) {
            this.defaultExpanded.push(item.id)
          }
        })
          this.alertTextArr = results;
        }).catch(error => {
          console.log("");
        });
    },
    aa(id, data) {
      var results = [];
      for (var i = 0, iLen = data.length; i < iLen; i++) {
        if (data[i].parentId == id) {
          var child = {
            label: data[i].groupName,
            id: data[i].id
          };
          child.children = this.aa(data[i].id, data);
          results.push(child);
        }
      }
      return results;
    },

    handleNodeClick(data) {
      console.log(data);
      this.currentPage = 1;
      this.newPage = 10;
      this.dataId = data.id;
      if (data.label == "") {
        this.treeList();
      } else {
        this.groupList();
      }
    },
    groupList() {
      axios({
        method: "post",
        url: "/apiGroup/listByParentId",
        data: {
          id: this.dataId,
          pageSize: 10,
          pageNum: this.currentPage
        }
      }).then(res => {
          this.tablegroup = res.data.data.list;
          this.pageSize = res.data.data.pageSize;
          this.total = res.data.data.total;
        }).catch(error => {
          console.log("");
        });
    },
    treeList() {
      axios({
        method: "post",
        url: "/apiGroup/listByParentId",
        data: {
          id: this.dataId == '' ? '' : this.dataId,
          pageSize: this.newPage =='' ? 10 :this .newPage,
          pageNum: this.currentPage
        }
      }).then(res => {
        if(res.data.code == 403){
          location.href = res.data.data;
        }

          if(!this.jurisdiction.roles.includes('admin')){
            this.delBtn = false;
            this.addBtnP = false;
            this.amendBtn = false;
          }else{
            this.delBtn = true;
            this.addBtnP = true;
            this.amendBtn = true;
            this.tablegroup = res.data.data.list;
            this.pageSize = res.data.data.pageSize;
            this.total = res.data.data.total;
          }
          // this.newPage = 10;
          // this.currentPage = 1;

      }).catch(error => {
        console.log("");
      });
    },
    //======================================,, start======================================
    initData() {
      axios({
        method: "post",
        url: "/apiGroup/pageInfoListByLevel",
        data: {
          groupName: this.searchGroupName == "" ? null : this.searchGroupName,
          pageSize: 10,
          pageNum: this.currentPage
        }
      }).then(res => {
        alert('del');
      }).catch(error => {
        console.log("");
      });
    },
    //======================================,,  end ======================================
    //======================================      start   ================================================
    addgroupList(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const groupName = this.addGroupData.groupName;
          const description = this.addGroupData.groupDescription;
          const parentId = this.addGroupData.parentId;
          axios({
            method: "post",
            url: "/apiGroup/save",
            data: {
              groupName: groupName,
              groupDescription: description,
              parentId: parentId
            }
          }).then(res => {
             if(res.data.code == 2000){
                this.creatGroup = false;
                this.searchGroupName = "";
                this.addGroupData.groupName = "";
                this.addGroupData.groupDescription = "";
                this.groupListRight();
                this.treeGroup();
             }else{
               this.$message({
                type: "info",
                message: res.data.message
              });
             }
            }).catch(error => {
              console.log("");
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    groupListRight(){
      axios({
        method: "post",
        url: "/apiGroup/listByParentId",
        data: {
          id: this.addGroupData.parentId,
          pageSize: 10,
          pageNum: this.currentPage
        }
      }).then(res => {
          this.tablegroup = res.data.data.list;
          this.pageSize = res.data.data.pageSize;
          this.total = res.data.data.total;
        }).catch(error => {
          console.log("");
        });
    },

    // ====================================  ==================================
    amendGroup(row) {
      this.amnedData.groupUUID = row.groupUUID;
      this.amendGroupDetail = true;
      this.amnedData.groupName = row.groupName;
      this.amnedData.groupDescription = row.groupDescription;
      this.rowArr = row;

    },
    amendData(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          axios({
            method: "post",
            url: "/apiGroup/update",
            data: {
              groupUUID: this.amnedData.groupUUID,
              groupName: this.amnedData.groupName,
              groupDescription: this.amnedData.groupDescription
            }
          }).then(res => {
              console.log(res, "");
              this.amendGroupDetail = false;
              this.treeGroup();
              this.groupListRight();
            }).catch(error => {
              console.log(error.message);
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    //
    closeDialog(done) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        done();
        location.reload();
      }).catch(_ => {});

    },
    callOf(formName) {
      this.creatGroup = false;
      this.$refs[formName].resetFields();
    },

    dateFormat(row, column) {
      const date = row[column.property];
      if (date == undefined) {
        return "";
      }
      return moment(date).format("YYYY-MM-DD HH:mm:ss");
    },
    //======================================       end    ======================================
    //======================================          start    =====================================
    showDeleteModel(index, row) {
      this.delGroupData = row;
      this.delGroupData.index = index;
      this.$confirm(` "${this.delGroupData.groupName}",!`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
          this.delGroupBtn();
      }).catch(() => {
        this.$message({
          type: "info",
          message: ""
        });
      });
    },

    delGroupBtn() {
      const delData = this.delGroupData;
      const index = this.delGroupData.index; //
      axios({
        method: "post",
        url: "/apiGroup/logicDelete",
        data: {
          groupUUID: delData.groupUUID
        }
      })
        .then(res => {
          if (res.data.code !== 2000) {
            this.delDialo = false;
            this.$message({
              message: res.data.message,
              type: "error"
            });
          } else {
            this.tablegroup.splice(index, 1);
            this.delDialo = false;
            this.treeGroup();
            this.groupListRight();
            this.$message({
              type: "success",
              message: ""
            });
          }
        })
        .catch(error => {
          console.log("");
        });
    },

    //======================================            end   ======================================
    //======================================           start  ======================================
    handleCurrentChange(val) {
      this.currentPage = val;
      this.treeList();
    },
    handleSizeChange(val) {
      this.newPage = val;
      this.treeList();
    },
    //======================================            end   ======================================
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    tableRowClassName(row, index) {
      if (index%2 !== 0) {
        return 'info-row';
      }
      return '';
    },
  }
};
</script>

<style scoped>
.treeBox {
  width: 15%;
  position: absolute;
  top: 0px;
  left: 0;
  border: 1px solid #dfe6ec;
  background: #EDEEF1;
  border-bottom: none;
}

.treeBox .treeGroup {
  width: 100%;
  height: 40px;
  line-height: 40px;
  display: inline-block;
  padding-left: 15px;
  border-bottom: 1px solid #dfe6ec;
  font-weight: bold;
  font-size:12px;
  color:#8792A3;
}

.box {
    width: 400px;
}
    .top {
      text-align: center;
    }

    .left {
      float: left;
      width: 60px;
    }

    .right {
      float: right;
      width: 60px;
    }

    .bottom {
      clear: both;
      text-align: center;
    }

    .item {
      margin: 4px;
    }

    .left .el-tooltip__popper,
    .right .el-tooltip__popper {
      padding: 8px 10px;
    }

</style>



