<template>
  <div class="flowMonitoringDetail" style="background:white;">

    <div class="detailBtn" style="padding:8px 10px 0;">
      <div class="fl">
        <span class="detailNav"></span>
        <router-link to="/flowMonitoring">
          <el-button size="small" style="width:auto"></el-button>
        </router-link>
      </div>
      <p style="clear:both"></p>
    </div>

    <div class="panel panel-default">

      <el-tabs type="border-card">
        <el-tab-pane label="">
          <div class="panel panel-default" style="position:relative">
            <el-button class="fr" type="primary" @click="bindApii()" size="small" style="margin-bottom:10px;">APP</el-button>
            <el-table ref="multipleTable" :data="authedList" border tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
              <el-table-column label="" width="120">
                <template slot-scope="scope">{{scope.row.id}}</template>
              </el-table-column>
              <el-table-column prop="appName" label=""></el-table-column>
              <el-table-column prop="appKey" label=""></el-table-column>
              <el-table-column prop="appSecret" label=""></el-table-column>
              <el-table-column prop="updateTime" label="" :formatter='dateFormat'></el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-button type="text" class="radiusBtn" size="small" @click='removeApp(scope.row.id)'></el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>

    <!--  start -->
    <el-dialog title="" :visible.sync="bindApi" size="small" id="accreditBox" @close='closeDialog'>
      <div class="accreditLine"></div>
      <el-form>
        <div>
          <span style="font-weight:700;"></span>
          <p></p>
          <div class="fl flBox" style="margin-bottom:20px;width: 100%">
            <el-form :inline="true" class="demo-form-inline">
              <el-form-item id="accreditInput">
                <el-input placeholder="" v-model='oderr'></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="getAppList()"></el-button>
              </el-form-item>
            </el-form>

            <el-table ref="multipleTable" :data="appList" border tooltip-effect="dark" style="width: 100%;height: 380px;overflow:auto" @selection-change="handleSelectionChange">
              <el-table-column type="selection" width="55" class="selection" prop='limitStrategyuuid' :selectable='checkboxInit' @selection-change="handleSelectionChange"></el-table-column>

              <el-table-column label="">
                <template slot-scope="scope">{{ scope.row.appName}}</template>
              </el-table-column>
              <el-table-column prop="appKey" label="appKey"></el-table-column>
              <el-table-column prop="createTime" label="" :formatter='dateFormat'></el-table-column>
            </el-table>
          </div>

        </div>
      </el-form>
      <p style="clear:both"></p>
      <div slot="footer" class="dialog-footer">
        <el-button @click="bindApi = false"> </el-button>
        <el-button type="primary" @click="setAuth()"> </el-button>
      </div>
    </el-dialog>

    <!--  start -->
    <el-dialog title="" :visible.sync="amendFluid" size="tiny" @close='closeModify'>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

        <el-form-item label="" :label-width="formLabelWidth" prop='sName'>
          <el-input class="w9000" v-model="ruleForm.sName"></el-input>
          <p>4~15</p>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth" prop='sUnit'>
          <el-select v-model="ruleForm.sUnit" placeholder="" class="w9000">
            <el-option :key="1" label="" :value="1"></el-option>
            <el-option :key="2" label="" :value="2"></el-option>
            <el-option :key="3" label="" :value="3"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="API" :label-width="formLabelWidth" prop='sNo'>
          <el-input class="w9000" v-model="ruleForm.sNo"></el-input>
           (API)
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="amendFluid  = false"> </el-button>
        <el-button type="primary" @click="modifyStrategy('ruleForm')"> </el-button>
      </div>
    </el-dialog>
    <!--  end -->
  </div>
</template>

<script>
import axios from 'axios'
import moment from 'moment'
export default {
  name: 'flowMonitoringDetail',
  components: {
  },
  data() {
    //------------------------------------//
    let sNameRule1 = (rule, value, callback) => {
      let regExp = /^[\u4e00-\u9fa5a-zA-Z][\u4e00-\u9fa5a-zA-Z\d]+$/;
      if (regExp.test(value) === false) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    //------------------------------------//
    let sNameRule2 = (rule, value, callback) => {
      let regExp = /^[\u4e00-\u9fa5a-zA-Z][\u4e00-\u9fa5a-zA-Z\d](?!_)(?!.*?_$)[a-zA-Z0-9_\u4e00-\u9fa5]+$/;
      if (regExp.test(value) === false) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    //------------------API------------------//
    let sNo = (rule, value, callback) => {
      let regExp = /^[0-9]*[1-9][0-9]*$/;
      if (value == null || value == '') {
        callback(new Error('API'));
      } else if (regExp.test(value) === false) {
        callback(new Error(''));
      } else if (value > 10000000) {
        callback(new Error('10000000()'));
      } else {
        callback();
      }
    };
    const generateData = _ => {
      const data = [];
      for (let i = 1; i <= 15; i++) {
        data.push({
          key: i,
          label: ` ${i}`,
          disabled: i % 4 === 0
        });
      }
      return data;
    };
    return {
      amendFluid: false, // 
      bindApi: false, //API
      currentPage4: 4,
      formLabelWidth: '120px',
      value: '',
      createTime: '',
      updateTime: '',
      strategyInfo: {},
      updateTimeAuthed: '',
      authedList: [],
      oderr: '',
      appList: [],
      delData: '',
      multipleSelection: [],
      data: generateData(),
      value3: [1],
      renderFunc(h, option) {
        return `<span>{ option.key } - { option.label }</span>`;
      },
      dateFormat(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss");
      },
      ruleForm: {
        sName: '',
        sNo: '',
        sUnit: ''
      },
      rules: {
        sName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 15, message: ' 4  15 ', trigger: 'blur' },
          { validator: sNameRule2, trigger: 'blur' }
        ],
        sUnit: [
          // { required: true, message: '', trigger: 'change' }
        ],
        sNo: [
          { validator: sNo, trigger: 'blur' }
        ]
      }

    };
  },

  created() {
    this.created();
  },

  methods: {
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleChange(value, direction, movedKeys) {
      console.log(value, direction, movedKeys);
    },
    //
    created() {
      let id = this.$route.query.id;
      axios({
        method: 'post',
        url: '/strategy/details',
        data: {
          id: id
        }
      }).then(res => {
        this.strategyInfo = res.data.data;
        this.createTime = moment(res.data.data.createTime).format("YYYY-MM-DD HH:mm:ss");
        this.updateTime = moment(res.data.data.updateTime).format("YYYY-MM-DD HH:mm:ss");
        this.ruleForm.sName = res.data.data.name;
        this.ruleForm.sNo = res.data.data.no;
        this.ruleForm.sUnit = res.data.data.unit;
      }).catch(error => {
        console.log('');
      });
      axios({
        method: 'post',
        url: '/strategy/getAllAuthed',
        data: {
          strategyUuid: this.$route.query.uuid
        }
      }).then(res => {
        this.authedList = res.data.data;
      }).catch(error => {
        console.log('');
      });
    },
    //
    modifyStrategy(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          axios({
            method: 'post',
            url: '/strategy/update',
            data: {
              id: this.strategyInfo.id,
              name: this.ruleForm.sName,
              unit: this.ruleForm.sUnit,
              no: this.ruleForm.sNo
            }
          }).then(res => {
            this.amendFluid = false;
            this.created();
          }).catch(error => {
            console.log('');
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    //App
    removeApp(id) {
      this.$confirm('app, ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        axios({
          method: 'post',
          url: '/strategy/removeApp',
          data: {
            id: id
          }
        }).then(res => {
          this.$message({
            type: 'success',
            message: '!'
          });
          this.created();
        }).catch(error => {
          console.log('');
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    bindApii() {
      this.bindApi = true;
      this.getAppList();
    },
    //appList
    getAppList() {
      axios({
        method: 'post',
        url: '/strategy/getStrategyList',
        data: {
          order: this.oderr == '' ? null : this.oderr
        }
      }).then(res => {
        this.appList = res.data.data.list;
      }).catch(error => {
        console.log('');
      });
    },
    //
    checkboxInit(row, index) {
      const uuid = row.limitStrategyuuid;
      if (uuid != null && uuid != '')
        return 0;
      else
        return 1;
    },
    //
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //
    setAuth() {
      var flag = 0;
      var ids = '';
      if (this.multipleSelection.length != 0) {
        this.multipleSelection.forEach(row => {
          if (flag == 0) {
            ids += row.id;
          } else {
            ids += (',' + row.id);
          }
          flag++;
        });
        axios({
          method: 'post',
          url: '/strategy/strategyAuth',
          data: {
            strategyUuid: this.strategyInfo.uuid,
            appIds: ids
          }
        }).then(res => {
          this.created();
        }).catch(error => {
          console.log('');
        });
      }
      this.bindApi = false;
    },
    //
    closeDialog() {
      this.oderr = '';
    },
    //
    closeModify() {
      this.ruleForm.sName = this.strategyInfo.name;
      this.ruleForm.sNo = this.strategyInfo.no;
      this.ruleForm.sUnit = this.strategyInfo.unit;
    }

  }
}

</script>

<style scoped>
.transfer-footer {
  margin-left: 20px;
  padding: 6px 5px;
}
</style>



