<template>
  <div class="flowMonitoring" style="background:#ffffff;position:relative;height:100%;">
    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane label="" name="first">

        <el-form :inline="true" class="search_form">
          <el-form-item>
            <el-select v-model="dictType" clearable placeholder="">
              <el-option v-for="item in typeNameOptions" :key="item.id" :label="item.label" :value="item.id"></el-option>
            </el-select>

            <el-input placeholder="" class="w350" v-model="dictCodes"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="info" class="searchOne" @click="initData()"> </el-button>
          </el-form-item>
        </el-form>

        <el-button type="primary addOne ml20" v-if="addJBtn" @click="addCode"></el-button>
        <el-button type="primary addOne" v-if="typeBtn" @click="addTypeData();typeData()"></el-button>
        <el-table ref="multipleTable" :data="codeData" tooltip-effect="dark" style="width:100%;margin-top:20px;" :row-class-name="tableRowClassName">
          <el-table-column prop="dictValue" label=""></el-table-column>
          <el-table-column prop="typeName" label=""></el-table-column>
          <el-table-column prop="dictName" label=""></el-table-column>
          <el-table-column prop="" label="" width="80">
            <template slot-scope="scope">
              <el-button type="text" v-if="delBtnO" class="radiusBtn delColor" size="small" @click="delCode(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="teCen mt60" v-if="this.codeData.length < 1">
          <p><img src="../../assets/Shape.png" /></p>
          <span></span>
        </div>

        <div class="page" style="padding-bottom:30px;" v-if="this.codeData.length > 0">
          <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
            :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
          </el-pagination>
        </div>
      </el-tab-pane>

      <el-tab-pane label="" name="second">
        <el-form :inline="true" class="search_form">
          <el-form-item>

            <el-select v-model="valueKey" clearable filterable placeholder="" @visible-change="slideList">
              <el-option v-for="item in optionsKey" :key="item.id" :label="item.label" :value="item.id"></el-option>
            </el-select>

            <el-input placeholder="" class="w350" v-model="dictCode"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="info" class="searchOne" @click="codeValMapTwo()"> </el-button>
          </el-form-item>
        </el-form>

        <el-button type="primary addOne ml20" v-if="addMBtn" @click="codeValMsg = true;"></el-button>
        <el-table ref="multipleTable" :data="codeValMap" tooltip-effect="dark" style="width:100%;margin-top:20px;" :row-class-name="tableRowClassName2">
          <el-table-column prop="dictValue" label="key"></el-table-column>
          <el-table-column prop="dictItemValue" label=""></el-table-column>
          <el-table-column prop="dictItemName" label=""></el-table-column>
          <el-table-column prop="" label="" width="80">
            <template slot-scope="scope">
              <el-button type="text" v-if="delBtnT" class="radiusBtn delColor" size="small" @click="delCodeMap(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="teCen mt60" v-if="this.codeValMap.length < 1">
          <p><img src="../../assets/Shape.png" /></p>
          <span></span>
        </div>

        <div class="page" style="padding-bottom:30px;" v-if="this.codeValMap.length > 0">
          <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange2" @current-change="handleCurrentChange2" :current-page="pageNum2"
            :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize2" layout=" sizes, prev, pager, next, jumper" :total="total2">
          </el-pagination>
        </div>


      </el-tab-pane>
    </el-tabs>

    <!--  start -->
    <el-dialog title="" :visible.sync="codeVal" size="tiny" style="top:-5%">
      <el-form :model="addCodeMes" ref="addCodeMes" class="demo-ruleForm">
        <el-form-item label="" prop="dictValueOne" :label-width="formLabelWidth" :rules="[
        { required: true, message: ''},
        { min: 1, max: 100, message: ' 1  100 ', trigger: 'blur' }]">
          <el-input auto-complete="off" v-model="addCodeMes.dictValueOne"></el-input>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth" :rules="[
        { required: true, message: ''}]">
          <el-select v-model="typeNameOptions2Res" placeholder="">
            <el-option v-for="item in typeNameOptions2" :key="item.id" :label="item.label" :value="item.id"></el-option>
          </el-select>
          <el-button type="text" class="ml35 addWcolor" style="color:#FEBD49" @click="addtypeBtn();"></el-button>
        </el-form-item>

        <el-form-item label="" prop="dictNamesDic" :label-width="formLabelWidth" :rules="[
        { required: true, message: ''},
        { min: 1, max: 100, message: ' 1  100 ', trigger: 'blur' }]">
          <el-input auto-complete="off" v-model="addCodeMes.dictNamesDic"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="callOfCode('addCodeMes')"> </el-button>
        <el-button type="primary" class="addOne" @click="addCodeVal('addCodeMes')"> </el-button>
      </div>
    </el-dialog>
    <!--   end -->

    <!--  start -->
    <el-dialog title="" :visible.sync="otherVal" size="tiny" style="top:-5%">
      <el-input auto-complete="off" placeholder="" v-model="addType" @change="validateT"></el-input>
      <el-table :data="typeDia" border style="width: 100%;height:250px;overflow:auto;">
        <el-table-column prop="name" label="" width="180">
        </el-table-column>
        <el-table-column prop="" label="">
          <template slot-scope="scope">
            <el-button type="text" class="radiusBtn amendColor" size="small" @click="amendCode(scope.row)"></el-button>
            <el-button type="text" class="radiusBtn delColor" size="small" @click="delType(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="otherVal = false"> </el-button>
        <el-button type="primary" class="addOne" @click="addTypeBtn()"> </el-button>
      </div>
    </el-dialog>
    <!--   end -->
    <!--  start -->
    <el-dialog title="" :visible.sync="amendType" size="tiny" style="top:-5%">
      <el-input v-model="typeName" placeholder=""></el-input>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="amendType = false"> </el-button>
        <el-button type="primary" class="addOne" @click="amendTypeBtn()"> </el-button>
      </div>
    </el-dialog>
    <!--   end  -->

    <!--  start -->
    <el-dialog title="" :visible.sync="codeValMsg" size="tiny" style="top:-5%">
      <el-form :model="codeMapMes" ref="codeMapMes" class="demo-ruleForm">
        <el-form-item label="key" :label-width="formLabelWidth" :rules="[
        { required: true, message: 'key'}]">
          <el-select v-model="valueKey2" filterable placeholder="">
              <el-option v-for="item in keyOptions" :key="item.id" :label="item.label" :value="item.id"></el-option>
            </el-select>
        </el-form-item>

        <el-form-item label="" prop="dictValueTwo" :label-width="formLabelWidth" :rules="[
        { required: true, message: ''},
        { min: 1, max: 100, message: ' 1  100 ', trigger: 'blur' }]">
          <el-input auto-complete="off" v-model="codeMapMes.dictValueTwo"></el-input>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth" prop="dictItemNameTwo" :rules="[
        { required: true, message: ''},
        { min: 1, max: 100, message: ' 1  100 ', trigger: 'blur' }]">
          <el-input auto-complete="off" v-model="codeMapMes.dictItemNameTwo"></el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="callOf('codeMapMes')"> </el-button>
        <el-button type="primary" class="addOne" @click="codeValMsgRes('codeMapMes')"> </el-button>
      </div>
    </el-dialog>
    <!--   end -->

  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "flowMonitoring",
  components: {},
  data() {
    return {
      jurisdiction:{},
      addJBtn:false,
      typeBtn:false,
      delBtnO:false,
      delBtnT:false,
      addMBtn:false,
      pageNum: 1,
      pageSize: 10,
      total: 0,
      pageNum2: 1,
      pageSize2: 10,
      total2: 0,
      dictType: "", //
      dictCodes: "", //
      dictKey: "", //key
      dictCode: "", //
      codeData: [],
      codeValMap: [],
      typeNameOptions: [], // 
      typeNameOptions2:[],
      keyOptions: [], //key
      keyOptionsId:'',
      typeNameOptionsArr: [],
      searchStrategy: "",
      searchType: "",
      searchCode: "",
      formLabelWidth: "120px",
      activeName: "first",
      codeVal: false,
      otherVal: false,
      codeValMsg: false,
      amendType: false,
      dictionaryId: "",
      value: "",
      form: {
        region: ""
      },
      addCodeMes: {},
      codeMapMes: {},
      addType: "", //
      typeDia: [],
      typeName: "",
      typeObj:{},
      typeNameOptions2Res:'',
      valueKey:'',
      valueKey2:'',
      optionsKey:[],
      arr:[],
      newPage:'',
      newPage2:'',

    };
  },

  created() {
    this.initData();
    this.typeData();
    this.dataList();
    this.getUserPermissions();
  },

  methods: {
    //
    getUserPermissions(){
      axios({
        method: "post",
        url: "/getCasUser",
        data: {}
      }).then(res => {
        this.jurisdiction  = res.data;
        if(this.jurisdiction.perms.length == 0 || this.jurisdiction.perms == null){
          this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            location.href = res.data.logoutUrl;
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        }
      }).catch(error => {
        console.log("");
      });
    },
    //==================================== 111 ====================================
    initData() {
      axios({
        method: "post",
        url: "/dict/query/dictionary",
        data: {
          typeId: this.dictType,
          dictValue: this.dictCodes,
          pageSize: this.newPage == '' ? 10 : this.newPage,
          pageNum: this.pageNum
        }
      }).then(res => {
        if(res.data.code == 403){
          location.href = res.data.data;
        }
        if(!this.jurisdiction.roles.includes('admin')){
          this.delBtnO = false;
          this.addJBtn = false; //Btn
          this.typeBtn = false; //Btn
        }else{
          this.delBtnO = true;
          this.addJBtn = true; //Btn
          this.typeBtn = true; //Btn
        }

        this.codeData = res.data.data.list;
        this.pageSize = res.data.data.pageSize;
        this.total = res.data.data.total;
      }).catch(error => {
        console.log("");
      });
    },
    dataList(){
      axios({
        method: "post",
        url: "/dict/query/dictionary",
        data: {
          pageSize: 10000,
        }
      }).then(res => {
          var list = res.data.data.list;
          this.keyOptions = [];
          list.map(data => {
            this.keyOptions.push({ label: data.dictValue, id: data.id });
          });
        }).catch(error => {
          console.log("");
        });
    },
    //
    addCode() {
      this.codeVal = true;
    },
    //
    addtypeBtn() {
      this.codeVal=false;
      this.otherVal=true;
    },
    addCodeVal(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.typeNameOptions.map(data => {
          this.typeNameOptionsArr = data;
        });
        this.addCodeMes.typeId = this.typeNameOptionsArr.id;
        axios({
          method: "post",
          url: "/dict/save/dictionary",
          data: {
            dictValue: this.addCodeMes.dictValueOne, //
            dictName: this.addCodeMes.dictNamesDic, //  
            typeId: this.typeNameOptions2Res
          }
        }).then(res => {
            if (res.data.code == 2000) {
              this.$refs[formName].resetFields();
              this.addCodeMes.dictValueOne = "";
              this.addCodeMes.dictNamesDic = "";
              this.typeNameOptions2Res = "";
              this.codeVal = false;
              this.initData();
              this.$message({
                message: "",
                type: "success"
              });
            } else {
              this.$message.error(res.data.message);
            }
          }).catch(error => {
            console.log("");
          });
        } else {
          return false;
        }
      });

    },
    //
    addTypeBtn() {
      var flag = true;
      if(this.addType.length > 200){
        this.$message.info('200');
        flag = false;
      }else{
        axios({
          method: "post",
          url: "/dict/save/type",
          data: {name: this.addType}
        }).then(res => {
          if(res.data.code == 2000){
            this.addType = "";
            this.initData();
            this.typeData();
          }else{
            this.$message({
            message: res.data.message,
            type: 'warning'
            });
          }
        }).catch(error => {
          console.log("");
        });
      }
    },
    //
    addTypeData(){
      this.otherVal=true;
    },
    //
    typeData() {
      axios({
        method: "post",
        url: "/dict/query/type",
        data: {
          pageSize:100,
        }
      }).then(res => {
          this.typeDia = res.data.data.list;
          var list = res.data.data.list;
          this.typeNameOptions = [];
          this.typeNameOptions2 = [];
          this.typeNameOptions.push({ label:'', id: '' });
          list.map(data => {
            this.typeNameOptions.push({ label: data.name, id: data.id });
            this.typeNameOptions2.push({ label: data.name, id: data.id });
          });
        }).catch(error => {
          console.log("");
        });
    },
    //
    delCode(row) {
    this.$confirm(`"${row.dictValue}", ?`, "", {
      confirmButtonText: "",
      cancelButtonText: "",
      type: "warning"
    }).then(() => {
        this.delCodeRes(row);
        this.$message({
          type: "success",
          message: "!"
        });
      }).catch(() => {
        this.$message({
          type: "info",
          message: ""
        });
      });
    },
    //
    delCodeRes(row) {
      axios({
        method: "post",
        url: "/dict/delete/dictionary",
        data: {
          id: row.id
        }
      }).then(res => {
        this.initData();
      }).catch(error => {
        console.log("");
      });
    },
    //
    amendCode(row) {
      this.amendType = true;
      this.typeName = row.name;
      this.typeObj = row;
    },
    amendTypeBtn() {
      axios({
        method: "post",
        url: "/dict/save/type",
        data: {
          name: this.typeName,
          id: this.typeObj.id
        }
      }).then(res => {
        if (res.data.code == 2000) {
          this.amendType = false;
          this.typeData();
          this.initData();
        } else {
          this.$message({
            message: res.data.message,
            type: 'warning'
          });
        }
      }).catch(error => {
        console.log("");
      });
    },

    //
    delType(row) {
          this.$confirm(`"${row.name}", ?`, "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).then(() => {
            this.delTypeRes(row);
            this.typeData();
            this.$message({
              type: "success",
              message: "!"
            });
          }).catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });

    },
    //
    delTypeRes(row) {
      axios({
        method: "post",
        url: "/dict/delete/type",
        data: {
          id: row.id
        }
      }).then(res => {
          console.log(res, "");
          this.typeData();
          // this.initData();
        }).catch(error => {
          console.log("");
        });
    },
    //==================================== 222 ====================================
    slideList(){
      this.codeOptions();
    },
    handleClick(tab, index) {
      if (tab.index == 1) {
        this.codeValMapTwo();
        this.dataList();
        this.getUserPermissions2();
      }
    },
    //
    getUserPermissions2(){
      axios({
        method: "post",
        url: "/getCasUser",
        data: {}
      }).then(res => {
        if(this.jurisdiction.perms == null){
          this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            location.href = res.data.logoutUrl;
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        }
      }).catch(error => {
        console.log("");
      });
    },

    codeValMapTwo() {
      axios({
        method: "post",
        url: "/dict/query/item",
        data: {
          dictItemValue: this.dictCode,
          dictionaryId:this.valueKey,
          pageSize: this.newPage2 == '' ? 10 : this.newPage2,
          pageNum: this.pageNum2
        }
      }).then(res => {
        if(res.data.code == 403){
          location.href = res.data.data;
        }

        if(!this.jurisdiction.roles.includes('admin')){
          this.delBtnT = false;
          this.addMBtn = false;
        }else{
          this.delBtnT = true;
          this.addJBtn = true; //Btn
          this.addMBtn = true; //Btn
        }

        this.codeValMap = res.data.data.list;
        this.pageSize2 = res.data.data.pageSize;
        this.total2 = res.data.data.total;
        this.optionsKey = [];
        this.optionsKey.push({ label:'', id: '' });
        }).catch(error => {
          console.log("");
        });
    },
    codeOptions(){
      axios({
        method: "post",
        url: "/dict/query/dictionary",
        data: {pageSize: 6000}
      }).then(res => {
        var list = res.data.data.list;
        this.optionsKey = [];
        this.optionsKey.push({ label:'', id: '' });
        list.map(data =>{
          this.optionsKey.push({ label:data.dictValue, id: data.id });
        })
        }).catch(error => {
          console.log("");
        });
    },


    codeValMsgRes(formName) {
      this.$refs[formName].validate(valid => {
      if (valid) {
      axios({
        method: "post",
        url: "/dict/save/item",
        data: {
          dictItemValue: this.codeMapMes.dictValueTwo,
          dictItemName: this.codeMapMes.dictItemNameTwo,
          dictionaryId: this.valueKey2
        }
      }).then(res => {
        if (res.data.code == 2000) {
          this.$refs[formName].resetFields();
          this.codeValMsg = false;
          this.valueKey2 = '';
          this.codeMapMes.dictValueTwo = "";
          this.codeMapMes.dictItemNameTwo = "";
          this.codeValMapTwo();
          this.$message({
            type: "success",
            message: "!"
          });
        } else {
          this.$message({
            type: "info",
            message: res.data.message
          });
        }
      }).catch(error => {
        console.log("");
      });
      } else {
        console.log("error submit!!");
        return false;
      }
      });
    },
    //
    delCodeMap(row) {
      this.$confirm(` "${row.dictItemValue}" ,!`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$message({
          type: "success",
          message: "!"
        });
        this.delStrategy(row);
      }).catch(() => {
        this.$message({
          type: "info",
          message: ""
        });
      });
    },

    delStrategy(row) {
      axios({
        method: "post",
        url: "/dict/delete/item",
        data: {
          id: row.id
        }
      }).then(res => {
          console.log(res, "222");
          this.codeValMapTwo();
      }).catch(error => {
        console.log("");
      });
    },

    //
    validateT(){
      if(this.addType.length > 200){
        this.$message.info('200');
      }
    },

    //
    handleCurrentChange(val) {
      this.pageNum = val;
      this.initData();
    },
    handleSizeChange(val){
      this.newPage = val;
      this.initData();
    },
    handleCurrentChange2(val) {
      this.pageNum2 = val;
      this.codeValMapTwo();
    },
    handleSizeChange2(val){
      this.newPage2 = val;
      this.codeValMapTwo();
    },
    callOf(formName) {
      this.codeValMsg = false;
      this.$refs[formName].resetFields();
    },
    callOfCode(formName) {
      this.codeVal = false;
      this.$refs[formName].resetFields();
    },
    tableRowClassName(row, index) {
      if (index%2 !== 0) {
        return 'info-row';
      }
      return '';
    },
    tableRowClassName2(row, index) {
      if (index%2 !== 0) {
        return 'info-row';
      }
      return '';
    },
  }
};
</script>

<style scoped>
</style>



